
import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler, TaskExecutionContext } from '@farris/devkit';
import { FlatFuncService } from '../../domain/index';
import { FlatFuncTreeStatemachine } from '../flat-func-tree-statemachine';
import { FlatFuncTreeUistate } from '../flat-func-tree-uistate';
import { FlatFuncTreeBindingdata } from '../flat-func-tree-bindingdata';
import { FlatFuncTreeRepository } from '../../domain/flat-func-tree-repository';

@Injectable()
@NgCommandHandler({
  commandName: 'cancel'
})
class FlatCancleHandler extends CommandHandler {

  constructor(
    private repository: FlatFuncTreeRepository,
    private bindingData: FlatFuncTreeBindingdata,
    private stateMachine: FlatFuncTreeStatemachine,
    private uiState: FlatFuncTreeUistate,
  ) {
    super();
  }

  schedule() {

    // 数据加载
    this.addTask('cancel', () => {
        if (this.uiState.action !== 'add') {
            return ;
        }
        const id = this.bindingData.list.currentId;
        this.repository.removeEntityById(id.toString());
        this.uiState.action = 'camcel';
      });


    // 状态迁移
    this.addTask('transitState', () => {
      this.stateMachine['view']();
    });
  }
}

export { FlatCancleHandler };



