import { Entity, NgField, NgObject, EntityList, NgList } from '@farris/devkit';

/**
 * 客户实体
 */
export class Invoking extends Entity {

  /**
   * type
   */
  @NgField({
    dataField: 'Type',
  })
  type: string;


  /**
   * Url
   */
  @NgField({
    dataField: 'Url',
  })
  Url: string;

    /**
   * Path
   */
  @NgField({
    dataField: 'Path',
  })
  path: string;

  /**
   * Image
   */
  @NgField({
    dataField: 'Image',
  })
  image: string;

    /**
   * Width
   */
  @NgField({
    dataField: 'Width',
  })
  width: Float32Array;

      /**
   * Height
   */
  @NgField({
    dataField: 'Height',
  })
  height: Float32Array;
}



