import { Injectable } from '@angular/core';
import { flatfunctionObjs } from './entities/flatdatas';
import { FlatFuncTreeDatastorage } from './flat-func-tree-datastorage';
import { FlatFuncTreeRepository } from './flat-func-tree-repository';
import { RestfulService } from '@farris/devkit';
import { HttpHeaders, HttpErrorResponse } from '@angular/common/http';

@Injectable()
export class FlatFuncService {

  private url = '/api/runtime/sys/v1.0/functions';
  private dataBody: any;
  public sessionid:string;

  constructor(
    private reposity: FlatFuncTreeRepository,
    private datastorage : FlatFuncTreeDatastorage,
    private rest: RestfulService,
  ) {
   }

  getdata(): any {
    const headers = new HttpHeaders().set('SessionId', this.sessionid);
    // this.datastorage.load(flatfunctionObjs);
    return this.rest.get(this.url, null, {headers}).subscribe((res: any) => {
      if (res instanceof Array) {
        res.forEach((item) => { // kendoUi treeview parentid 为第一级的时候 必须undefined
          if (item.ParentId === '0') {
            item.ParentId = undefined;
          }
        });
        this.datastorage.load(res);
      } else {
        if (res.ParentId === '0') {
          res.ParentId = undefined;
        }
        this.datastorage.load([res]);
      }
    });
  }

  /**
   * 保存
   */
  postFunc(item: any) {

    // todo:此处争取顺序应该是先httpservice 更新服务端，然后客户端重新load
    const headers = new HttpHeaders().set('SessionId', this.sessionid);
    const entities =  this.reposity.getAllEntities();
    entities.forEach(element => {
      if (element.FID === item.FID) {
        element.FCode = item.FCode;
        element.FBizObj = item.FBizObj;
        element.FName = item.FName;
        element.FParentid = item.FParentid;
        element.FInvokingConfig = item.FInvokingConfig;
        element.FIsWorkStation = item.FIsWorkStation;
        element.FIcon = item.FIcon;
        element.FLayer = item.FLayer;
        element.FPath = item.FPath;
        element.FuncType = item.FuncType;
        this.dataBody = this.convert2Funcobj(item);      
      }      
    });
    this.rest.post(this.url, this.dataBody, null, {headers}).subscribe((res: any) => {
      if(res) {
        this.getdata();
      }
    },
    (error: HttpErrorResponse) => {
      alert(error.error.Message);
    });
    // this.reposity.addEntity(item);
    // this.datastorage.load(entities);
  }

  updateFunc(item: any) {
    var headers = new HttpHeaders().set("SessionId", this.sessionid);
    this.dataBody = this.convert2Funcobj(item);
    this.rest.put(this.url, this.dataBody, null, {headers}).subscribe((res: any) => {
    });
  }


  /**
   * 删除并重新加载
   */
  remove(id: string) {
      // todo:此处争取顺序应该是先httpservice 更新服务端，然后客户端重新load
      // this.reposity.removeEntityById(id);
      const headers = new HttpHeaders().set('SessionId', this.sessionid);
      this.rest.delete(`${this.url}/${id}`, null, {headers}).subscribe((res: any) => {
        if (res) {
          this.reposity.removeEntityById(id);
        }
      });
  }

    convert2Funcobj(item: any): any {
      const data = {
        Id: item.FID,
        Code: item.FCode,
        BizObjectId: item.FBizObj,
        BizOpId: item.FBizOpId,
        MenuType: item.FMenuType,
        Name: item.FName,
        Path: item.FPath || '',
        ParentId: item.FParentid || '0',
        InvokingConfig: item.FInvokingConfig,
        IsWorkStation: item.FIsWorkStation,
        IsDetail: item.FIsDetail,
        IsSysMenu: item.FIsSysMenu,
        Icon: item.FIcon,
        Layer: item.FLayer || '',
        FuncType: item.FuncType,
        Creator: ''
      };
      return data;
    }

  clonefunction(item: any) {
  }
}



