import { Injector, Injectable } from '@angular/core';
import { NgViewModel, ViewModel,  NgCommand } from '@farris/devkit';

import { FlatFuncTreeBindingdata } from './flat-func-tree-bindingdata';
import { FlatFuncTreeForm } from './flat-func-tree-form';
import { FlatFuncTreeStatemachine } from './flat-func-tree-statemachine';
import { FlatFuncTreeUistate } from './flat-func-tree-uistate';

/**
 * 视图模型
 */
@Injectable()
@NgViewModel({
  children: [],
  binding: FlatFuncTreeBindingdata,
  form: FlatFuncTreeForm,
  stateMachine: FlatFuncTreeStatemachine,
})
class FlatFuncTreeViewmodel extends ViewModel {

  public uiState: FlatFuncTreeUistate;
  public stateMachine: FlatFuncTreeStatemachine;

  /**
   * 构造函数
   */
  constructor(injector: Injector) {
    super(injector);
    this.uiState = this.injector.get(FlatFuncTreeUistate);
  }

  /**
   * 表单加载
   */
  @NgCommand({
    name: 'formLoad'
   })
  public formLoad() {}

  /**
   * 新增子级
   */
  @NgCommand({
    name: 'addchild'
   })
  public addchild() {}

  /**
   * 新增平级
   */
  @NgCommand({
    name: 'additem'
   })
  public additem() {}

  /**
   * 编辑
   */
  @NgCommand({
    name: 'edit'
   })
  public edit() {}


  /**
   * 删除
   */
  @NgCommand({
    name: 'remove'
   })
  public remove() {}

  /**
   * 保存
   */
  @NgCommand({
    name: 'save'
   })
  public save() {}

  /**
   * 取消
   */
  @NgCommand({
    name: 'cancel'
   })
  public cancel() {}


}

export { FlatFuncTreeViewmodel };



