import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { FlatFunctionTreeComponent } from './flat-function-tree.component';

const routes: Routes = [
  { path: '', component: FlatFunctionTreeComponent },
  { path: '/:ssid', component: FlatFunctionTreeComponent }
];

@NgModule({
  imports: [
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
class FlatFuncTreeRoutingModule {}

export { FlatFuncTreeRoutingModule };



