import { Injectable, Injector } from '@angular/core';
import { NgBindingData, BindingData } from '@farris/devkit';
import { FlatFuncEntity, FlatFuncTreeRepository } from '../domain/index';

@Injectable()
@NgBindingData({
  entity:     FlatFuncEntity,
  repository: FlatFuncTreeRepository
})
class FlatFuncTreeBindingdata extends BindingData {
  constructor(injector: Injector) {
    super(injector);
  }
}

export { FlatFuncTreeBindingdata };



