import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import { FlatFuncTreeViewmodel } from './app/flat-func-tree-viewmodel';
import { Change, ChangeType } from '@farris/devkit';
import { ParamMap, ActivatedRoute} from '@angular/router';
import { FlatFuncService, FlatFuncTreeRepository } from './domain/index';
import { FrameworkService } from '@gsp-sys/rtf-common';

@Component({
  selector: 'app-function-tree',
  templateUrl: './flat-function-tree.component.html',
  styleUrls: ['./flat-function-tree.component.css']
})
export class FlatFunctionTreeComponent implements OnInit {

  public data: any[];
  currentid: string;
  sessionid: string;
  funcid: string;
  tParam: any;

  isSysMenue: boolean;
  sysMenuNum: number;
  isworkstation: boolean;
  workStationNum: number;
  isUnionFunc = false;
  unionFuncNum: number;

  isAppGroup: boolean;
  isApp: boolean;
  isFuncGroup: boolean;
  isFunc: boolean;

  public fetchChildren(node: any): Observable<any[]> {
    return of(node.items);
  }

  public hasChildren(node: any): boolean {
    return node.items && node.items.length > 0;
  }

  constructor( 
    public viewmodel: FlatFuncTreeViewmodel, 
    private repos: FlatFuncTreeRepository,
    private route: ActivatedRoute, 
    private fservice: FlatFuncService, 
    private frmservice: FrameworkService ) {
  }
  ngOnInit() {
    this.route.data
    .subscribe((data: { funcParam: any }) => {
      this.tParam = data.funcParam;
    });
    this.route.paramMap.subscribe((params: ParamMap) => {
      if (params.has('funcid')) {
        this.funcid = params.get('funcid');
        this.sessionid = this.frmservice.getSession(this.funcid);
        this.fservice.sessionid = this.sessionid;
      }
    });

    this.viewmodel.bindingData.changes.subscribe((changes: Change ) => {
      console.log('changetype =' + changes.type);

      if (changes.type === ChangeType.SelectionChanged) {
         return;
      }

      if (changes.type === ChangeType.Append) {
        let newItem = this.viewmodel.bindingData.list.currentItem;
        this.funcTypeSetting(newItem);
        this.menuTypeSetting(newItem);
     }

      const a = changes;
      this.data = this.viewmodel.bindingData.list.toJSON();
      console.log(JSON.stringify(this.data));
    });

    this.viewmodel.form.valueChanges.subscribe(
      (changes: Change ) => {
        console.log('changetype =' + changes.type);
        const a = changes;
        this.data = this.viewmodel.bindingData.list.toJSON();
        console.log(JSON.stringify(this.data));
    });

    this.viewmodel.formLoad();
  }

  handleSelection({ dataItem }: any): void {
    const config = dataItem.FInvokingConfig;
    console.log(config);
    this.menuTypeSetting(dataItem);
    this.funcTypeSetting(dataItem);
    this.viewmodel.bindingData.list.setCurrentId(dataItem.FID);
  }

  /**
   * 设定展现层级
   * @param param 
   */
  FuncTypeChanged(param: any) {
    let funcTypeNum = 0;
    let t = param.target;
    let tid = t.id;
    let checkRes = t.checked;

    this.isAppGroup = false;
    this.isApp = false;
    this.isFuncGroup = false;
    this.isFunc = false;

    switch (tid) {
      case 'ck1':
        this.isAppGroup = checkRes;
        if(checkRes) {
          funcTypeNum = 1;
        }
        break;

      case 'ck2':
        this.isApp = checkRes;
        if(checkRes) {
          funcTypeNum = 2;
        }
        break;

      case 'ck3':
        this.isFuncGroup = checkRes;
        if(checkRes) {
          funcTypeNum = 3;
        }
        break;

      case 'ck4':
        this.isFunc = checkRes;
        if(checkRes) {
          funcTypeNum = 4;
        }
        break;
    }
    
    let currentId = this.viewmodel.bindingData.list.currentItem.getValue('FID');
    let item = this.repos.getEntityById(currentId);
    item.FuncType = funcTypeNum.toString();
  }

  /**
   *  设置菜单类型
   * @param param 
   */
  MenuTypeChanged(param: any) {
    let t = param.target;
    let tid = t.id;
    let checkRes = t.checked;

    switch (tid) {

      case 'ch1':
        this.isSysMenue = checkRes;
        if(checkRes) {
          this.sysMenuNum = 1;
        } else {
          this.sysMenuNum = 0;
        }
        break;

      case 'ch2':
        this.isUnionFunc = checkRes;
        if(checkRes) {
          this.unionFuncNum = 2;
        } else {
          this.unionFuncNum = 0;
        }
        break;

      case 'ch2':
        this.isworkstation = checkRes;
        if(checkRes) {
          this.workStationNum = 4;
        } else {
          this.workStationNum = 0;
        }
        break;
    }

    const menuType = this.sysMenuNum + this.unionFuncNum + this.workStationNum;
    let currentId = this.viewmodel.bindingData.list.currentItem.getValue('FID');
    let item = this.repos.getEntityById(currentId);
    item.FMenuType = menuType;
    if(this.sysMenuNum === 1) {
      item.FIsSysMenu = true;
    } else {
      item.FIsSysMenu = false;
    }
  }

  funcTypeSetting(dataItem: any) {
    this.isAppGroup = false;
    this.isApp = false;
    this.isFuncGroup = false;
    this.isFunc = false
 
    switch(dataItem.FuncType) {

      case '1': this.isAppGroup = true; break;

      case '2': this.isApp = true; break;

      case '3': this.isFuncGroup = true; break;

      case '4': this.isFunc = true; break;
    }
  }

  menuTypeSetting(dataItem: any) {
    this.isSysMenue = dataItem.FIsSysMenu;
    this.isUnionFunc = false;
    this.isworkstation = false;
    this.sysMenuNum = 0;
    this.workStationNum = 0;
    this.unionFuncNum = 0;
   
    switch(dataItem.FMenuType) {

      case 1: 
        this.isSysMenue = true;
        this.sysMenuNum = 1;
        break;

      case 2:
        this.isUnionFunc = true;
        this.unionFuncNum = 2;
        break;

      case 3:
        this.isSysMenue = true;
        this.isUnionFunc = true;
        this.sysMenuNum = 1;
        this.unionFuncNum = 2;
        break;

      case 4:
        this.isworkstation = true;
        this.workStationNum = 4;
        break;

      case 5:
        this.isSysMenue = true;
        this.isworkstation = true;
        this.sysMenuNum = 1;
        this.workStationNum = 4;
        break;

      case 6:
        this.isUnionFunc = true;
        this.isworkstation = true;    
        this.unionFuncNum = 2;
        this.workStationNum = 4;
      break;

      case 7:
        this.isSysMenue = true;
        this.isUnionFunc = true;
        this.isworkstation = true;
        this.sysMenuNum = 1;
        this.workStationNum = 4;
        this.unionFuncNum = 2;
        break;
    }
  }
}



