
import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler, TaskExecutionContext , EntityFactory} from '@farris/devkit';
import { FlatFuncService } from '../../domain/index';
import { FlatFuncTreeStatemachine } from '../flat-func-tree-statemachine';
import { FlatFuncTreeUistate } from '../flat-func-tree-uistate';
import { FlatFuncEntity } from '../../domain/index';
import { DataUtil } from '../../shared/data-util';
import { FlatFuncTreeBindingdata } from '../flat-func-tree-bindingdata';
import { FlatFuncTreeRepository } from '../../domain/flat-func-tree-repository';

@Injectable()
@NgCommandHandler({
  commandName: 'addchild'
})
class FlatAddChildHandler extends CommandHandler {

  constructor(
    private uiState: FlatFuncTreeUistate,
    private stateMachine: FlatFuncTreeStatemachine,
    private repository: FlatFuncTreeRepository,
    private bindingData: FlatFuncTreeBindingdata
  ) {
    super();
  }

  schedule() {

    // 数据加载
    this.addTask('addchild', () => {
      this.uiState.action = 'add';
      const item = this.bindingData.list.currentItem;
      const data = {
        Id: DataUtil.createNM(),
        Code : '',
        Name: '' ,
        ParentId: item['FID'],
        Layer: Number(item['FLayer']) + 1,
        FuncType: '0',
        MenuType: 0,
        IsDetail: true,
        InvokingConfig: {
          'Type': '',
          'Url': '',
          'Path': '',
          'Image': '',
          'Width': 0.0,
          'Height': 0.0
        }
      };
      const newEntity = EntityFactory<FlatFuncEntity>(FlatFuncEntity, data);

      this.repository.addEntity(newEntity);
    });

    // 状态迁移
    this.addTask('transitState', () => {
      this.stateMachine['edit']();
    });
  }
}

export { FlatAddChildHandler };



