import { Entity, NgField, NgObject, EntityList, NgList } from '@farris/devkit';
import { Invoking } from './invokingconfig';
/**
 * 客户实体
 */
export class FlatFuncEntity extends Entity {

  /**
   * Id
   */
  @NgField({
    dataField: 'Id',
    primary: true
  })
  FID: string;

  /**
   * code
   */
  @NgField({
    dataField: 'Code'
  })
  FCode: string;

  /**
   * name
   */
  @NgField({
    dataField: 'Name'
  })
  FName: string;


  /**
   * 业务对象
   */
  @NgField({
    dataField: 'BizObjectId'
  })
  FBizObj: string;


    /**
   * 业务操作
   */
  @NgField({
    dataField: 'BizOpId'
  })
  FBizOpId: string;


  /**
   * 是否是工作中心
   */
  @NgField({
    dataField: 'IsWorkStation'
  })
  FIsWorkStation: boolean;

  @NgField({
    dataField:'IsDetail'
  })
  FIsDetail: boolean;

  @NgField({
    dataField:'IsModule'
  })
  FIsModule: boolean;

  @NgField({
    dataField:'IsSysMenu'
  })
  FIsSysMenu: boolean;


  @NgObject({
    dataField: 'InvokingConfig',
    type : Invoking
  })
  FInvokingConfig: Invoking;


  // /**
  //  * 调用信息
  //  */
  // @NgField({
  //   dataField:'InvokingConfig'
  // })
  // FInvokingConfig: string;

  /**
   * 层级
   */
  @NgField({
    dataField: 'Layer'
  })
  FLayer: string;

  /**
   * 分级码
   */
  @NgField({
    dataField: 'Path'
  })
  FPath: string;

  /**
   * 展现层级
   */
  @NgField({
    dataField: 'FuncType'
  })
  FuncType: string;

  /**
   * 图标
   */
  @NgField({
    dataField: 'Icon'
  })
  FIcon: string;


  /**
   * 父节点  //NgField、NgList、NgObject
   */
  @NgField({
    dataField: 'ParentId',
  })
  FParentid: string;

  @NgField({
    dataField: 'MenuType'
  })
  FMenuType: number;
}



