import { Injectable, Injector } from '@angular/core';
import { FormControl } from '@angular/forms';
import { NgForm, Form, NgFormControl, NgChildForm, NgChildFormArray } from '@farris/devkit';
import { FlatFuncTreeBindingdata} from './flat-func-tree-bindingdata';

@Injectable()
@NgForm({
  bindingData: FlatFuncTreeBindingdata
})
class FlatFuncTreeForm extends Form {

  constructor(injector: Injector) {
    super(injector);
  }

  /**
   * id
   */
  @NgFormControl({
    name: 'Id',
    binding: 'FID'
  })
  Id: FormControl;

  /**
   * code
   */
  @NgFormControl({
    name: 'code',
    binding: 'FCode'
  })
  Code: FormControl;

  /**
   * 名称
   */
  @NgFormControl({
    name: 'name',
    binding: 'FName'
  })
  Name: FormControl;

  /**
   * 业务对象
   */
  @NgFormControl({
    name: 'bizobj',
    binding: 'FBizObj'
  })
  BizObj: FormControl;

    /**
   * 业务操作
   */
  @NgFormControl({
    name: 'bizop',
    binding: 'FBizOpId'
  })
  FBizOpId: FormControl;

    /**
   * 图标
   */
  @NgFormControl({
    name: 'icon',
    binding: 'FIcon'
  })
  FIcon: FormControl;

    /**
   * 工作中心
   */
  @NgFormControl({
    name: 'isworkstation',
    binding: 'FIsWorkStation'
  })
  FIsWorkStation: FormControl;

  @NgFormControl({
    name: 'ismodel',
    binding: 'FIsModule'
  })
  FIsModule: FormControl;

  @NgFormControl({
    name: 'isdetail',
    binding: 'FIsDetail'
  })
  FIsDetail: FormControl;

  @NgFormControl({
    name: 'issysmenu',
    binding: 'FIsSysMenu'
  })
  FIsSysMenu: FormControl;

    /**
   * 调用信息
   */
  @NgFormControl({
    name: 'invokingtype',
    binding: 'FInvokingConfig.type'
  })
  FInvokingType: FormControl;

    /**
   * 调用信息
   */
  @NgFormControl({
    name: 'invokingurl',
    binding: 'FInvokingConfig.Url'
  })
  FInvokingUrl: FormControl;

    /**
   * 调用信息
   */
  @NgFormControl({
    name: 'invokingpath',
    binding: 'FInvokingConfig.path'
  })
  FInvokingPath: FormControl;
  // /**
  //  * 调用信息
  //  */
  @NgFormControl({
    name: 'invokingconfig',
    binding: 'FInvokingConfig'
  })
  FInvokingConfig: FormControl;

    /**
   * 调用信息
   */
  @NgFormControl({
    name: 'layer',
    binding: 'FLayer'
  })
  FLayer: FormControl;

   /**
   * 分级码
   */
  @NgFormControl({
    name: 'path',
    binding: 'FPath'
  })
  FPath: FormControl;

  /**
   * 展现层级
   */
  @NgFormControl({
    name: 'funcType',
    binding: 'FuncType'
  })
  FuncType: FormControl;
}


export {FlatFuncTreeForm };



