import { Injectable, Injector } from '@angular/core';
import { NgRepository, Repository } from '@farris/devkit';
import { FlatFuncEntity } from './entities/flat-func.entity';
import { FlatFuncTreeDatastorage } from './flat-func-tree-datastorage';

@Injectable()
@NgRepository({
  from: FlatFuncTreeDatastorage,
  entityType: FlatFuncEntity
})
class FlatFuncTreeRepository extends Repository<FlatFuncEntity> {
  constructor(injector: Injector) {
    super(injector);
  }
}

export { FlatFuncTreeRepository };



