import { Provider } from '@angular/core';
import { FlatFuncTreeBindingdata } from './flat-func-tree-bindingdata';
import { FlatFuncTreeForm } from './flat-func-tree-form';
import { FlatFuncTreeStatemachine } from './flat-func-tree-statemachine';
import { FlatFuncTreeViewmodel } from './flat-func-tree-viewmodel';
import { FlatFuncTreeUistate } from './flat-func-tree-uistate';
import { commandHandlers } from './handler/index';


const APP_PROVIDERS: Provider[] = [
    FlatFuncTreeBindingdata,
    FlatFuncTreeForm,
    FlatFuncTreeStatemachine,
    FlatFuncTreeViewmodel,
    FlatFuncTreeUistate
];

export {
    FlatFuncTreeBindingdata,
    FlatFuncTreeForm,
    FlatFuncTreeStatemachine,
    FlatFuncTreeViewmodel,
    FlatFuncTreeUistate,

    APP_PROVIDERS,
    commandHandlers
};



